            link: 'https://www.meetup.com/Hiking-near-Munich/events/261608978/',
            description: '<p>Hey y´all</p> <p>For everyone who is not joining on 30th May or wanting something a little longer: Here we go :)</p> <p>Hoping the weather will be okay - I would love to go to one of my most loved places with you!</p> <p>We would meet at 07:45am at the Central Station; in front of the lower Starbucks (North Entrance of Main Hall, take staircase to mid-level just before you head to S-Bahn level)</p> <p>The tour itself is at 19km, 270m gain. It should take around 6-7 hours, depending on how relaxed we take it and how long we stay at the Biergarten for lunch. You can either take something to eat with you and buy something. We might also be able to have an early dinner at Kloster Andechs.</p> <p>Tipp:<br/>Take swimming gear with you, if you feel like taking a dive into the water :)</p> <p>Train Journey Details:<br/>Departure 08.32 Uhr / S6 to Starnberg, Exit at Starnberg<br/>Return / S8 from Herrsching</p> <p>Ticket Information:<br/>Please make sure to have proper MVG Tickets. We will have the opportunity to buy group-day-tickets when meeting; depending on the number of people joining.</p> <p>What is important:</p> <p>- Trekking Shoes to be recommended<br/>- enough water and snacks for along the way<br/>- swimming gear :)</p> <p>Kindly note that end of RSVP is on Friday, 12th July at 12pm. Anyone changing the status afterwards to no without a short email to me or anyone just not showing up will be marked as no show as a reference for future smaller hikes.</p> ',
