            link: 'https://www.meetup.com/Hiking-near-Munich/events/258384273/',
            description: '<p>[UPDATE]<br/>Let´s meet on Wednesday, 8th May at 06.30pm to talk routes, arrangements, etc. I would send out an email with the location prior to this day.</p> <p>[INFORMATION]<br/>I am currently in the process of booking my accomodation. For everyone who is fairly certain that he/she would like to join. Feel free to send me an email and I will let you know where to book.</p> <p>The Lechweg offers an extraordinary nature experience; it accompanies the Lech for almost 125km. From its spring near the Formarinsee in the Austrian Alps to the Lechfall in Füssen, Germany.</p> <p>This is one of my challenges for 2019 and I would love to have some company! Are you up for it? :)</p> <p>This MeetUp is functioning as framework as I really would love the idea of getting to know you beforehand and even plan together. The below route is just an idea and can be changed to whatever suits us best.</p> <p>I would suggest for a meetup in a coffee shop - maybe around March - to discuss details and routes and locations to stay overnight.</p> <p>I am actually already really excited. Although I am suffering an injured ankle at the moment :) :) :) Looking forward to meeting you all!</p> <p>Current Route:<br/>Day 1/Wednesday 28th August<br/>Journey to Innerbraz (to be reached by train from Munich via Bludenz)</p> <p>Day 2/Thursday, 29th August - Mountain Day<br/>Innerbraz to Freibürger Hütte (Formarinsee) - 13km - 1´2300m elevation with overnight stay - Very good fitness required. Mostly accessible paths. Sure-footedness required.</p> <p>Day 3/Friday, 30th August - Lech Day<br/>Freibürger Hütte to Lech - 14km - 650m elevation - easy hike<br/>Overnight Stay in Lech am Arlberg</p> <p>Day 4/Saturday, 31st August - Mountain Day<br/>Lech am Arlberg to Stuttgarter Hütte - 10km - 1´100m elevation, uphill - Sure-footedness, sturdy shoes and alpine experience required</p> <p>Day 5/Sunday, 1st September- Mountain Day<br/>Stuttgarter Hütte to Steeg - 14km - 1240m elevation, downhill - Good fitness required. Mostly accessible paths. Sure-footedness required</p> <p>Day 6/Monday, 2nd September - Lech Day<br/>Steeg via Holzgau to Elbigenalp - 15km - 200m elevation along the river</p> <p>Day 7/ Tuesday, 3rd September - Lech Day<br/>Elbigenalp to Vorderhornbach - 17km - 400m elevation along the river</p> <p>Day 8/ Wednesday, 4th September - Lech Day<br/>Vorderhornbach to Weißenbach - 14,5km - 100m elevation along the river</p> <p>Day 9/Thursday, 5th September - Lech Day<br/>Weißenbach to Pflach - 14km - 100m elevation partly along the river and via panorama trail</p> <p>Day 10/ Friday, 6th September - Lech Day<br/>Pflach to Füssen via Alpsee and View to Neuschwanstein - 13km - 400m elevation</p> <p>Day 11/ Saturday, 7th September<br/>Journey back home</p> <p>Obviously I am not a trained tour leader of any kind. So participation is on your own responsibility. Each of us will be responsible for their own bookings in regards to train tickets, overnight stays etc.</p> ',
