            link: 'https://www.meetup.com/Munchen-Kickball/events/thdxzqyzjbvb/',
            description: '<p>Hey neuer oder routinierter Kickball Player ;)</p> <p>Wir warten auf dich wie üblich um 13 Uhr an der südwestlichen Ecke des chinesischen Turms. Wir sind diejenigen mit dem roten Kickball-Ball:</p> <p>goo.gl/maps/Pqp9ecnv3Xx</p> <p>Wenn du ein bisschen später auftauchen kannst, komm einfach spontan auf das Fels. Es liegt etwas nördlich vom Chinesischen Turm:<br/>goo.gl/maps/DQjErwppk8z</p> <p>Fühl dich frei deine Freunde mitzubringen! Zusätzliche Spieler und neue Gesichter sind immer willkommen. Essen und Getränke kannst du gern mitbringen.</p> <p>Wir freuen uns, dich am Sonntag zu sehen!</p> <p>----------------------------</p> <p>Hey Gang!</p> <p>Feel free to bring blankets, drinks, and food if you like. Also feel free to bring friends! Extra players and new faces are always welcome.</p> <p>Meet at the Chinese Tower at 1 pm, southwest corner. We\'ll be the weirdos carrying the big red kickballs:</p> <p><a href=\'https://goo.gl/maps/Pqp9ecnv3Xx\' class=\'linkified\'>https://goo.gl/maps/Pqp9ecnv3Xx</a></p> <p>If you show up a bit late you can meet us at the field, which is a bit north of the Chinese Tower:</p> <p><a href=\'https://goo.gl/maps/DQjErwppk8z\' class=\'linkified\'>https://goo.gl/maps/DQjErwppk8z</a></p> <p>Looking forward to seeing you all on Sunday!</p> <p>- Scott</p> ',
