            link: 'https://www.meetup.com/Munich-Explorers-Club/events/261960976/',
            description: '<p>Dear all,</p> <p>It is with great pleasure that I come back to Munich to visit for a few days after a trip to the Faroe Islands!</p> <p>It would be great to see some old faces and new from the Munich Explorers Club. Let\'s plan an informal (and perhaps unusual) get-together for Friday, June 21, starting with dinner at the Gasthaus Alter Wirt in Thalkirchen. From there we can walk down to the Isar and stake out a spot along the riverbank just before dark. Bring your favorite personal travel or adventure story to tell, and we will take turns passing the candle (or flashlight). Feel free to bring blankets, drinks, etc.</p> <p>Hope to see you there!</p> <p>Steve</p> ',
