            link: 'https://www.meetup.com/Munich-Hash-House-Harriers/events/hggczqyzjbmc/',
            description: '<p>A trail is set by a \'hare\' usually in flour and/or chalk and then the \'pack\' has to try and find \'true trail\' at junctions. It is like a treasure hunt/ Schnitzel Lauf. The trails are therefore ideal for all levels of running with faster runners able to run more to check out the correct way and slower runners given the opportunity to catch up. We are not a road running group and our trails often take us into the forests or even off trail completely. It is a good way to get into fun running as it tends to be quite stop and go, rather than a continuous run.</p> <p>We are now on summer time. Don\'t forget to bring mozzie spray for the circle. There will be cars to store your bags.</p> <p>We\'ll post info here as it becomes available for the next run.</p> <p>You can also go to our regular website at <a href=\'http://www.munich-h3.eu \' class=\'linkified\'>http://www.munich-h3.eu</a></p> <p>or subscribe to the mailing list at [masked]</p> <p>We promise not to spam you. :-)</p> ',
