            description: '<p>Let\'s take photos of Munich\'s busy streets!</p> <p>We can share our thoughts about street-photography and practice a little taking photos of strangers in small groups.</p> <p>At the beginning, we will visit some touristic places such as the Viktualienmarkt, where people are used to being photographed. Once we have build up a little momentum and confidence in taking pictures of people on the street, we can venture to Karlsplatz and the Hauptbahnhof area to observe the different faces of Munich downtown!</p> <p>We will end at a cafe/restaurant and have a nice chat afterwards :)</p> ',
