            link: 'https://www.meetup.com/Munich-International-Hiking/events/261079103/',
            description: '<p>Soiernhaus is our destination in June. We will hike up two of Karwendel\'s most famous peaks: Schöttelkarspitze and Soiernspitze. Both days require previous hiking experience, a head for heights, and proper hiking boots. Strictly no trainers or mid-height footwear. I\'m kindly asking First Timers to join at least two medium to difficult day hikes before coming on this overnight hike. Thank you.</p> <p>ROUTE</p> <p>Day 1 - Saturday 22/06/2019<br/>Krün (850m) - Schwarzkopf (1150m) - Seinskopf (1961m) - Schöttelkarspitze (2050m) - Soiernhaus and Soiernsee (1600m). Walking time = ~5h, distance = 11km, elevation gain = 1250hm. This is a medium hike. Please bring a packed lunch along.</p> <p>A sheet sleeper is required to spend the night at Soiernhaus. I booked 10x Matratzenlager for us.</p> <p>Day 2 - Sunday 23/06/2019<br/>Soiernhaus (1600m) - Schöttelkarspitze (2050m) – Reißende Lahnspitze (2170m) – Soiernspitze (2253m) - Soiernhaus (1600m) - Fischbachalm (1400m) - Krün (850m). Walking time = ~8h, distance = 19km, elevation gain = 900hm. This is a difficult hike. We will have lunch at Soiernhaus before we hike back to Krün.</p> <p>COST ESTIMATE</p> <p>- €20 night at Soiernhaus (€10 for DAV members)<br/>- €30 share of train and bus ticket (price of the train ticket will be split evenly between the group)<br/>- A deposit of €20 (€10 for DAV members) is required to secure your spot<br/>- I\'d strongly recommend to bring ~€100 of cash along to pay for your lunch, dinner, and breakfast</p> <p>Please transfer the deposit by Friday 07/06/2019 to secure your spot. You can pay by PayPal or bank transfer (please send me a PM). Please select “private” when paying by PayPal otherwise I’ll be charged a fee of €1.50.</p> <p><a href=\'http://paypal.me/sarahdheldt\' class=\'linkified\'>http://paypal.me/sarahdheldt</a></p> <p>The organiser has to pay a deposit for all ten beds when booking. Most mountain huts don\'t offer a refund if the booking is cancelled less than a week before the booking. Therefore your deposit is not refundable, either.</p> <p>MEETING POINT</p> <p>München Hbf, next to Radius Tours, 07:45am on Saturday 22.June.2018, our train leaves at 07:59.<br/>Return to Munich: Sunday 23.June.2019 ~ 8pm-ish</p> <p>--<br/><a href=\'http://www.sektion-hochland.de/soiernhaus/index.php?id=soiernhaus\' class=\'linkified\'>http://www.sektion-hochland.de/soiernhaus/index.php?id=soiernhaus</a></p> ',
