            description: '<p>Beim letzten Fotowalk haben wir beschlossen, uns das nächste Mal im Hofgarten zu treffen.</p> <p>Wir treffen uns am Pavillion im Hofgarten um 18:30 Uhr, fotografieren dann dort ein bisschen und laufen dann bis zum Chinesischen Turm im Englischen Garten. Ende Juni ist hoffentlich so gutes Wetter, dass wir dort noch zusammen im Biergarten sitzen können.</p> <p>Wie immer geht es uns beim Fotowalk um den Austausch, das gemeinsame Fotografieren, das Entdecken von tollen Motiven in der Gruppe.</p> <p>Wir freuen uns auf einen schönen Walk,<br/>Simone und Mona</p> ',
